<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :append-to-body="true" title="部门导入" width="500px">
      <div class="p15">
        您可以先
        <a href="javascript: void(0);"
           class="btn"
           @click="download">下载模板</a>
        输入后再导入
        <el-row class="mt10">
          <el-col :span="20">
            <upload-image
              :config="uploadConfig"
              :file-list="fileList"
              @on-success="uploadHandler"></upload-image>
          </el-col>
        </el-row>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button @click="closeDialog('close')">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import uploadImage from '@/components/upload-image'

  export default {
    components: {uploadImage},
    props: ['dialogFormVisible'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      }
    },
    data() {
      return {
        uploadConfig: {
          accept: 'excel/xlsx',
          noteMessage: '请上传xslx'
        },
        businessLicense: [],
        downloadShow: false,
        fileList: []
      }
    },
    methods: {
      uploadHandler(rsp, file, fileList, vm) {
        this.fileList = [file]
        console.log(this.fileList)
      },
      closeDialog(method) {
        this.$emit('closeDialog', this.file, method)
      },
      download() {
        this.downloadShow = true
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog', {}, 'close')
      }
    }
  }
</script>
<style lang="scss">
  .dialog_input_custom {
    > input {
      height: 28px;
      font-size: 12px;
      top: 1px;
      position: relative;
    }
  }

  .dialog_date_custom {
    .el-input__prefix {
      top: -6px;
    }
  }
</style>


